import React, { PureComponent } from "react";
import $ from "jquery";
import _ from "lodash";
import Util from "./Util";
import csv from "csv-parser";

class CoinInfo extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      coinId: props.match.params.name,
      coinData: undefined
    };
  }

  onError(e) {
    $(e.target).remove();
  }

  componentDidMount() {
    var coinId = this.state.coinId;
    if (Util.exchangeData.length === 0) {
      var coinDataPromise = fetch(`..\\coinInfo.csv`).then(async (res) => {
        return await csv(res);
      });
      var latestDataPromise = fetch(`..\\coinValuesLatest.csv`).then(res =>
        csv(res)
      );
      var poolDataPromise = fetch(`..\\poolDataLatest.csv`).then(res =>
        csv(res)
      );
      var poolInfoPromise = fetch(`..\\poolInfo.csv`).then(res => csv(res));
      var exchangePromise = fetch(`..\\exchangeData.csv`).then(res =>
        csv(res)
      );

      Promise.all([
        coinDataPromise,
        latestDataPromise,
        poolInfoPromise,
        poolDataPromise,
        exchangePromise
      ]).then(values => {
        Util.generalCoinData = values[0];
        Util.latestCoinData = values[1];
        Util.exchangeData = values[4];

        const coinData = _.find(Util.generalCoinData, function(o) {
          return (
            coinId.toLowerCase() ===
            o["symbol"].toLowerCase() +
              "-" +
              o["coinName"].replace(/\s|'/g, "").toLowerCase()
          );
        });

        const coinLatestData = _.find(Util.latestCoinData, [
          "coinId",
          coinData.coinId
        ]);

        Util.generalPoolData = values[2];
        Util.latestPoolData = values[3];

        if (_.isUndefined(coinData)) return;

        const poolList = _.filter(Util.generalPoolData, ["coinId", coinData.coinId]);
        _.each(poolList, (value, key) => {
          const poolData = _.find(Util.latestPoolData, ["poolId", value.poolId]) || {
          };
          _.assign(poolList[key], poolData);
        });

        const exchangeList = _.filter(Util.exchangeData, [
          "coinId",
          coinData.coinId
        ]);

        this.setState({
          coinData: Util.getCoinData(coinData, coinLatestData),
          poolList,
          exchangeList,
          changeTime: new Date().getTime()
        });
      });
    } else {
      const coinData = _.find(Util.generalCoinData, function(o) {
        return (
          coinId.toLowerCase() ===
          o["symbol"].toLowerCase() +
            "-" +
            o["coinName"].replace(/\s|'/g, "").toLowerCase()
        );
      });

      const coinLatestData = _.find(Util.latestCoinData, [
        "coinId",
        coinData.coinId
      ]);

      if (_.isUndefined(coinData)) return;

      const poolList = _.filter(Util.generalPoolData, ["coinId", coinData.coinId]);
      _.each(poolList, (value, key) => {
        const poolData = _.find(Util.latestPoolData, ["poolId", value.poolId]) || {};
        _.assign(poolList[key], poolData);
      });

      const exchangeList = _.filter(Util.exchangeData, ["coinId", coinData.coinId]);

      this.setState({
        coinData: Util.getCoinData(coinData, coinLatestData),
        poolList,
        exchangeList,
        changeTime: new Date().getTime()
      });
    }
  }

  render() {
    const { coinData, poolList } = this.state;

    if (_.isUndefined(coinData)) {
      return (
        <div className="container-fluid">
          <div className="pr-3 pl-3 pt-3 pb-0">
            <div>Coin data missing!</div>
          </div>
        </div>
      );
    }

    var imgSrc = "/logos/resize/" + coinData.symbol.toLowerCase() + ".png";

    var sortedArray = [];
    _.each(coinData.links, function(value, key) {
      sortedArray.push({ linkKey: key, linkValue: value });
    });

    sortedArray = _.sortBy(sortedArray, ["linkKey"]);

    var generalLinks = [];
    _.each(sortedArray, function(link) {
      var value = link.linkValue;
      var key = link.linkKey;
      var className = "fab fa-2x fa-" + key;
      var linkText = _.capitalize(key);

      if (key === "website") {
        className = "fas fa-2x fa-home";
      }
      if (key === "bitcointalkAnn") {
        className = "fab fa-2x fa-bitcoin";
        linkText = "BitcoinTalk Announcement";
      }
      if (key === "repo") {
        className = "fab fa-2x fa-github";
        linkText = "Source Code";
      }

      generalLinks.push(
        <li className="p-2" key={key}>
          <span className="fa-li"><i className={className} /></span>
          {" "}
          <a
            href={value}
            target="_blank"
            rel="noopener noreferrer"
            title={linkText}
          >
            {linkText}
          </a>
        </li>
      );
    });

    let poolElements = [];
    _.each(poolList, (value, key) => {
      poolElements.push(
        <tr key={key}>
          <td><a href={value.url}>{value.url}</a></td>
          <td>{value.miners}</td>
          <td>{Util.millify(value.hashrate, 3)}</td>
        </tr>
      );
    });

    let poolHeader = "";

    if (poolElements.length) {
      poolHeader = <h2>Pools</h2>;
    }

    poolElements = (
      <table className="table table-bordered">
        <thead>
          <tr>
            <th>
              Pool
            </th>
            <th>
              Miners
            </th>
            <th>
              Hashrate
            </th>
          </tr>
        </thead>
        <tbody>
          {poolElements}
        </tbody>
      </table>
    );

    let exchangeElements = [];
    _.each(coinData.exchangeIds, (exchangeId, key) => {
      const exchangeName = Util.exchangeNames[exchangeId];
      const exchange = _.find(Util.exchangeData, (value, index) => {
        if (value.exchangeId === exchangeId && value.coinId === coinData.coinId) {
          return value;
        }
      })

      if (!exchange) return;

      exchangeElements.push(
        <tr key={key}>
          <td>
            <a href={Util.exchangeLinks[exchangeName]}>{exchangeName}</a>
          </td>
          <td>
            {exchange.activePairs}
          </td>
          <td>
            {exchange.bestPair}
          </td>
          <td>
            {Number(exchange.satoshiPrice).noExponents()}
          </td>
          <td>
            {Number(exchange.usdPrice).noExponents()}
          </td>
          <td>
            {Number(exchange.sellTotal).noExponents()}
          </td>
          <td>
            {Number(exchange.sellTotalBtc).noExponents()}
          </td>
          <td>
            {Number(exchange.buyTotal).noExponents()}
          </td>
          <td>
            {Number(exchange.buyTotalBtc).noExponents()}
          </td>
          <td>
            {new Date(exchange.created).toISOString()}
          </td>
        </tr>
      );
    });

    let exchangeHeader = "";

    if (exchangeElements.length) {
      exchangeHeader = <h2>Exchanges</h2>;
    }

    exchangeElements = (
      <table className="table table-bordered">
        <thead>
          <tr>
            <th>
              Exchange
            </th>
            <th>
              Active Pairs
            </th>
            <th>
              Best Pair
            </th>
            <th>
              BTC
            </th>
            <th>
              USD
            </th>
            <th>
              Total Coins on Sale
            </th>
            <th>
              Total BTC on Sale
            </th>
            <th>
              Total Coins on Order
            </th>
            <th>
              Total BTC on Order
            </th>
            <th>
              Pull Time
            </th>
          </tr>
        </thead>
        <tbody>
          {exchangeElements}
        </tbody>
      </table>
    );

    return (
      <div className="container-fluid">
        <div className="pl-3 pr-3 pt-3 pb-0">
          <h1>
            <img
              alt={coinData.coinName + " logo"}
              src={imgSrc}
              onError={this.onError}
            />
            {" "}
            {coinData.symbol}
            {" "}
            {coinData.coinName}
          </h1>
          <h2>Links</h2>
          <ul className="fa-ul">
            {generalLinks}
          </ul>
          {poolHeader}
          {poolElements}
          {exchangeHeader}
          {exchangeElements}
        </div>
      </div>
    );
  }
}

export default CoinInfo;
