import React, { PureComponent } from "react";
import _ from "lodash";
import VirtualCoinList from "./VirtualCoinList";

class CoinList extends PureComponent {
  constructor(props) {
    super(props);

    this.filterProps = this.filterProps.bind(this);
    this.filterCoins = this.filterCoins.bind(this);
    this.handleSearch = this.handleSearch.bind(this);

    this.state = {
      coinElements: [],
      coinElementsFiltered: [],
      hiddenByAdvancedOptions: 0,
      searchFilterText: undefined
    };
  }

  filterProps(props) {
    let hiddenByAdvancedOptions = 0;
    const coinElements = [];

    if (props.inactiveCoins) {
      _.each(props.coins, (coin) => {
        coinElements.push(coin);
      });
      this.setState({
        coinElements: coinElements,
        coinElementsFiltered: coinElements,
        hiddenByAdvancedOptions
      });
    }

    _.each(props.coins, (coin, index) => {
      if (_.get(props.activeAlgos, coin.algorithm) === "true") {
        if (
          coin.onExchange &&
          props.showTraded === "true" &&
          coin.buyTotalUsdRaw > parseFloat(props.minimumUsdBuyOrders)
        ) {
          let exActive = false;
          _.each(coin.exchangeNames, n => {
            if (_.result(props.stateActiveExchanges, n, "false") === "true") {
              exActive = true;
            }
          });
          if (exActive) {
            coinElements.push(coin);
            return;
          }
        }

        if (!coin.onExchange && props.showUntraded === "true") {
          coinElements.push(coin);
          return;
        }
      }

      if (coin.enabled === 1) {
        hiddenByAdvancedOptions++;
      }
    });

    let coinElementsFiltered;
    if (this.state.searchFilter && this.state.searchFilter.length > 0) {
      coinElementsFiltered = this.filterCoins();
    } else {
      coinElementsFiltered = coinElements;
    }

    this.setState({
      coinElements,
      coinElementsFiltered,
      hiddenByAdvancedOptions
    });
  }

  filterCoins() {
    return _.filter(this.state.coinElements, coin => {
      if (coin.coinName.toLowerCase().indexOf(this.state.searchFilter.toLowerCase()) !== -1) {
        return true;
      } else if (
        coin.symbol.toLowerCase().indexOf(this.state.searchFilter.toLowerCase()) !== -1
      ) {
        return true;
      }
    });
  }

  componentWillMount() {
    this.filterProps(this.props);
  }

  componentWillReceiveProps(newProps) {
    this.setState(
      {
        searchFilter: ""
      },
      () => {
        this.filterProps(newProps);
      }
    );
  }

  handleSearch(e) {
    this.setState(
      {
        searchFilter: e.target.value
      },
      () => {
        this.setState({
          coinElementsFiltered: this.filterCoins()
        });
      }
    );
  }

  render() {
    const {
      coinElements,
      coinElementsFiltered,
      hiddenByAdvancedOptions,
      searchFilterText
    } = this.state;

    const hiddenBySearch = coinElements.length - coinElementsFiltered.length;
    const coinsDisplay = coinElementsFiltered.length;

    const tableStyle = { display: "table", height: "100%" };
    const cellStyle = { display: "table-cell", verticalAlign: "middle" };

    let advancedOptionsFilterCountEl = '';
    if (!this.props.inactiveCoins) {
      advancedOptionsFilterCountEl =
        <div className="pl-3">
          <div style={tableStyle}>
            <em style={cellStyle}>
              Advanced options are filtering out
              {" "}
              {hiddenByAdvancedOptions}
              {" "}
              coins.
            </em>
          </div>
        </div>
    }

    return (
      <div>
        <div className="row pl-3 pr-3 pt-3 pb-0">
          <div className="pl-3">
            <div className="input-group">
              <div className="input-group-prepend">
                <span className="input-group-text">
                  Filter by Name or Symbol
                </span>
              </div>
              <input
                id="filterSearch"
                type="text"
                className="form-control"
                placeholder="Search"
                onChange={this.handleSearch}
                value={searchFilterText}
              />
            </div>
          </div>
          <div className="pl-3">
            <div style={tableStyle}>
              <em style={cellStyle}>
                Search query is filtering out {hiddenBySearch} coins.
              </em>
            </div>
          </div>
          {advancedOptionsFilterCountEl}
          <div className="pl-3">
            <div style={tableStyle}>
              <em style={cellStyle}>{coinsDisplay} coins shown.</em>
            </div>
          </div>
        </div>
        <div>
          <VirtualCoinList coins={coinElementsFiltered} inactiveCoins={this.props.inactiveCoins} showExtraDetails={this.props.showExtraDetails} />
        </div>
      </div>
    );
  }
}

export default CoinList;
