import React, { PureComponent } from "react";
import _ from "lodash";
import CoinList from "./CoinList";
import Util from "./Util";
import csv from "csv-parser";

class Inactive extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      coins: []
    };
  }

  componentDidMount() {
    // if (Util.generalCoinData.length === 0) {
    var inactiveDataPromise = fetch(`..\\coinInfo.csv`).then(res =>
      csv(res)
    );
    var latestDataPromise = fetch(`..\\coinValuesLatest.csv`).then(res =>
      csv(res)
    );

    Promise.all([inactiveDataPromise, latestDataPromise]).then(values => {
      Util.generalCoinData = values[0];
      Util.latestCoinData = values[1];

      var latest = [];
      const extra = [];
      _.each(Util.generalCoinData, value => {
        const coin = _.find(Util.latestCoinData, ["coinId", value.coinId]);
        if (!value.enabled) {
          const parsedCoin = Util.getCoinData(value, coin);
          if (parsedCoin.updatedRaw) {
            latest.push(parsedCoin);
          } else {
            extra.push(parsedCoin);
          }
        }
      });

      latest.sort(function(coinA, coinB) {
        return coinB.updatedRaw - coinA.updatedRaw;
      });

      latest = latest.concat(extra);

      this.setState({
        coins: latest,
        changeTime: new Date().getTime()
      });
    });
  }

  render() {
    const { coins } = this.state;
    const { showExtraDetails } = this.props;
    const inactiveCoins = coins.length;

    return (
      <div className="container-fluid">
        <div className="pb-0 pr-3 pt-3 pl-3">
          There are
          {" "}
          {inactiveCoins}
          {" "}
          inactive coins. These are ordered by the last time we received data for their blockchain most recent first. Of the last few coins they might be actually active but we've never recieved data for them.
        </div>
        <CoinList
          coins={coins}
          inactiveCoins={true}
          showExtraDetails={showExtraDetails === "true"}
        />
      </div>
    );
  }
}

export default Inactive;
