  sortByNewest(newState) {
    this.setState(prevState => {
      var order = 1;
      // if (prevState.sort === 'newest') {
      var sort = "newest";
      localStorage.setItem("order", order);
      localStorage.setItem("sort", sort);

      const time = newState.time || prevState.time;
      const parsedCoinData =
        newState.parsedCoinData || prevState.parsedCoinData;
      const algorithms = newState.algorithms || prevState.algorithms;
      const timeouts = newState.timeouts || prevState.timeouts;

      const coins = Util.sortByNewest(
        parsedCoinData[time],
        order,
        algorithms,
        timeouts[time]
      );

      return {
        coins,
        sort,
        order,
        time,
        changeTime: new Date().getTime(),
        ...newState
      };
    });
  }

