import React, { PureComponent } from 'react';
import $ from 'jquery';
import _ from 'lodash';
// import { Link } from 'react-router-dom';

class InactiveCoinCard extends PureComponent {
  onError(e) {
    $(e.target).remove();
  }

  render() {
    const { coin } = this.props;

    var headerClass = 'card-header text-white bg-info';
    if (coin.onExchange !== 'exchange') {
      headerClass = 'card-header text-white bg-danger';
    }

    headerClass = 'card-header';

    var cardBodyClass = 'card-body';
    if (coin.asicFriendly) {
      cardBodyClass = 'card-body asic-friendly';
    }

    var imgSrc = "logos/resize/"+coin.symbol.toLowerCase()+".png";

    if (coin.links && !coin.links.bitcointalkAnn && coin.bitcointalkAnn) {
      coin.links.bitcointalkAnn = coin.bitcointalkAnn
    }

    var sortedArray = [];
    _.each(coin.links, function(value, key) {
      sortedArray.push({ linkKey: key, linkValue: value })
    });

    sortedArray = _.sortBy(sortedArray, ['linkKey']);

    var generalLinks = [];
    _.each(sortedArray, function(link) {
      var value = link.linkValue;
      var key = link.linkKey;
      var className = "fab fa-2x fa-"+key;
      var linkText = _.capitalize(key);

      if (key === "website") {
        className = "fas fa-2x fa-home";
      }
      if (key === "bitcointalkAnn") {
        className = "fab fa-2x fa-bitcoin";
        linkText = "BitcoinTalk Announcement";
      }
      if (key === "repo") {
        className = "fab fa-2x fa-github";
        linkText = "Source Code";
      }

      generalLinks.push(<li className="p-2" key={key}><span className="fa-li"><i className={className}></i></span> <a href={value} target="_blank" rel="noopener noreferrer" title={linkText}>{linkText}</a></li>);
    });

    var coinLink = '/coins/'+coin.symbol.toLowerCase()+'-'+coin.coinName.replace(/\s|'/g,'').toLowerCase();
    var bgImage = {
      backgroundImage: `url('${imgSrc}')`
    }
    var lastSeen = '';

    if (coin.difficulty) {
      lastSeen = <p>
        <b>Last Seen:</b>
        <br/> {coin.updated}
        <br/> {coin.difficulty} difficulty
        <br/> {coin.networkHashrate} network hashrate
      </p>;
    }

    return (
      <div className="CoinCard card coin">
        <div className="inner rounded">
          <a className="header-link" href={coinLink}>
            <div className={headerClass}>
              <span className="float-right"><i className="fas fa-info-circle"></i></span>
              {/* <h3><img src={imgSrc} width="50" height="50" className="logoName" alt={coin.symbol} onError={this.onError} />{coin.coinName}</h3> */}
              <h3 style={bgImage}>{coin.coinName}</h3>
            </div>
          </a>
          <div className={cardBodyClass}>
            <div className="card-text">
              <p>
                {coin.algorithm}
              </p>
              {lastSeen}
              <ul className="fa-ul">
                {generalLinks}
              </ul>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default InactiveCoinCard;
