import CoinCard from "./CoinCard";
import InactiveCoinCard from "./InactiveCoinCard";
import React, { Component } from "react";
import _ from "lodash";
import {
  List,
  AutoSizer,
  CellMeasurer,
  CellMeasurerCache,
  WindowScroller
} from "react-virtualized";
import "react-virtualized/styles.css"; // only needs to be imported once

class VirtualCoinList extends Component {
  constructor(props) {
    super(props);
    this.renderRow = this.renderRow.bind(this);
    this.updateDimensions = this.updateDimensions.bind(this);

    this.cache = new CellMeasurerCache({
      defaultHeight: 800,
      fixedWidth: true
    });

    this.state = {
      colCount: -1,
      list: []
    };
  }

  renderRow({ index, key, style, parent }) {
    return (
      <CellMeasurer
        key={key}
        cache={this.cache}
        parent={parent}
        columnIndex={0}
        rowIndex={index}
      >
        <div style={style}>
          <div className="d-flex align-content-stretch flex-row">
            {_.map(this.state.list[index], (coin, index) => {
              if (this.props.inactiveCoins) {
                return <InactiveCoinCard key={index} coin={coin} />;
              }
              return <CoinCard key={index} coin={coin} showExtraDetails={this.props.showExtraDetails} />;
            })}
          </div>
        </div>
      </CellMeasurer>
    );
  }

  updateDimensions() {
    let colCount = 4;
    if (window.innerWidth <= 700) {
      colCount = 1;
    } else if (window.innerWidth <= 1000) {
      colCount = 2;
    } else if (window.innerWidth <= 1200) {
      colCount = 3;
    }
    if (this.state.colCount !== colCount) {
      var coinsChunked = _.chunk(this.props.coins, colCount);
      if (this.listRef) {
        this.listRef.forceUpdateGrid();
      }
      this.setState({ colCount, list: coinsChunked });
    }
  }

  componentWillMount() {
    this.updateDimensions();
  }

  componentDidMount() {
    window.addEventListener("resize", this.updateDimensions);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updateDimensions);
  }

  shouldComponentUpdate(nextProps, nextState) {
    if (this.state.colCount !== nextState.colCount) {
      return true;
    }
    return false;
  }
  componentWillReceiveProps() {
    this.setState(
      {
        colCount: -1
      },
      this.updateDimensions
    );
  }

  render() {
    var styles = {
      display: "block",
      position: "relative",
      paddingRight: "15px"
    };

    if (this.state.list.length < 1) {
      // this.setState({list:[]})
      // this.updateDimensions();
      // this.setState({list:[]}, this.updateDimensions);
      return <div />;
    }

    // if (this.state.colCount === -1) {

    this.cache.clearAll();

    return (
      <div style={styles}>
        {/* <div className="coinlist d-flex align-content-stretch flex-wrap flex-row"> */}
        <WindowScroller>
          {({
            height,
            isScrolling,
            registerChild,
            onChildScroll,
            scrollTop
          }) => (
            <AutoSizer disableHeight>
              {({ width }) => (
                <div ref={registerChild}>
                  <List
                    ref={ref => this.listRef = ref}
                    overscanRowCount={2}
                    noRowsRenderer={this._noRowsRenderer}
                    rowCount={this.state.list.length}
                    deferredMeasurementCache={this.cache}
                    rowHeight={this.cache.rowHeight}
                    rowRenderer={this.renderRow}
                    width={width}
                    autoHeight={true}
                    isScrolling={isScrolling}
                    onScroll={onChildScroll}
                    scrollTop={scrollTop}
                    height={height}
                    scrollToIndex={0}
                  />
                </div>
              )}
            </AutoSizer>
          )}
        </WindowScroller>
        {/* </div> */}
      </div>
    );
  }
}

export default VirtualCoinList;
