  "Cryptopia": "https://www.cryptopia.co.nz/Exchange/?market=<%= symbol.toUpperCase() %>_<%= pair.toUpperCase() %>",
  "Southxchange": "https://www.southxchange.com/Market/Book/<%= symbol.toUpperCase() %>/<%= pair.toUpperCase() %>",
  "TradeOgre": "https://tradeogre.com/exchange/<%= pair.toUpperCase() %>-<%= symbol.toUpperCase() %>",
  "StocksExchange": "https://app.stocks.exchange/en/basic-trade/pair/<%= pair.toUpperCase() %>/<%= symbol.toUpperCase() %>/1D",
  "Octaex": "",
  "TradeSatoshi": "https://tradesatoshi.com/Exchange/?market=<%= symbol.toUpperCase() %>_<%= pair.toUpperCase() %>",
  "Qryptos": "",
  "Cryptohub": "https://cryptohub.online/market/<%= symbol.toUpperCase() %>/",
  "Livecoin": "https://www.livecoin.net/trade/index?currencyPair=<%= symbol.toUpperCase() %>%2F<%= pair.toUpperCase() %>",
  "BTC-Alpha": "https://btc-alpha.com/exchange/<%= symbol.toUpperCase() %>_<%= pair.toUpperCase() %>/",
  "Crex24": "https://crex24.com/exchange/<%= symbol.toUpperCase() %>-<%= pair.toUpperCase() %>",
  "Palitanx": "",
  "Altex": "",
  "MapleChange": "https://maplechange.com/markets/<%= symbol.toLowerCase() %><%= pair.toLowerCase() %>",
  "BitexLive": "https://bitexlive.com/exchange/<%= pair.toUpperCase() %>-<%= symbol.toUpperCase() %>",
  "Kompler": "https://www.kompler.exchange/#/markets/<%= pair.toUpperCase() %>/<%= symbol.toUpperCase() %>",
  "BitexBay": "https://www.bitexbay.com/Home/Orders/index/currency/<%= symbol.toUpperCase() %>-<%= pair.toUpperCase() %>.html",
  "BitexBook": "https://bitexbook.com/trading/<%= pair.toLowerCase() %><%= symbol.toLowerCase() %>",
  "Graviex": "https://graviex.net/markets/<%= symbol.toLowerCase() %><%= pair.toLowerCase() %>",
  "Bittrex": "https://bittrex.com/Market/Index?MarketName=<%= pair.toUpperCase() %>-<%= symbol.toUpperCase() %>",
  "FirstCryptoBank": "https://exfcb.com/",
